<template>
  <div id="app">
    <!-- <img src="./assets/logo.png"> -->
    <router-view v-if="isRouterAlive"/>
  </div>
</template>

<script>
export default {
  computed:{		
			// userName() {
		  //       let localData = localStorage.userId;		       		   
		  //       if(this.$store.state.userId===undefined){		        	
		  //         	this.$store.commit('setUserId',localData) 
		  //       }		       
		  //       return this.$store.state.userId
		  //    }
		},

  name: 'App',
  provide(){
    return{
      reload:this.reload
    }
  },
  data(){
    return{
      isRouterAlive:true
    }
  },
  methods:{
    reload(){
      this.isRouterAlive = false
      this.$nextTick(function(){
        this.isRouterAlive = true
      })
    }
  }
}
</script>

<style>
#app {
  width: 100%;
  height: 100%;
}
</style>



