import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
    isCollapse: false,
}

const mutations = {
    isCollapse(state,value){
        state.isCollapse = value
    },
}

export default new Vuex.Store({
    state,mutations
})




