import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Main',
      redirect:'/zgmb',
      component: resolve=>require(["@/components/main.vue"], resolve),
      meta: {
        isLogin: true  // 是否需要判断是否登录,这里是需要判断
      },
      children:[
        {
          path: '/zgmb',
          name: 'zgmb',
          component: resolve=>require(["@/components/zhengaimubiao-1.vue"], resolve),
          meta: {keepAlive: true},
        },
        {
          path: '/zgbz',
          name: 'zgbz',
		      component: resolve=>require(["@/components/zhengaibiaozhun.vue"], resolve),
        },
        {
          path: '/zgjx',
          name: 'zgjx',
		      component: resolve=>require(["@/components/zhengaijixiao.vue"], resolve),
        },
        {
          path: '/add',
          name: 'add',
          component: resolve=>require(["@/components/addProject.vue"], resolve),
        },
        {
          path: '/detail',
          name: 'detail',
          component: resolve=>require(["@/components/detail-1.vue"], resolve),
          // meta: {keepAlive: true}
        },
        {
          path: '/tree',
          name: 'tree',
          component: resolve=>require(["@/components/projectTree.vue"], resolve),
          // meta: {keepAlive: true},
        },
        {
          path: '/edit',
          name: 'edit',
          component: resolve=>require(["@/components/edit.vue"], resolve),
        },
        {
          path: '/resolve',
          name: 'resolve',
		      component: resolve=>require(["@/components/resolve.vue"], resolve),
        },
        {
          path: '/file_detail',
          name: 'wjxq',
          component: resolve=>require(["@/components/fileDetail.vue"], resolve),
        }
      ]
    }
  ]
})



